<template>
  <div class="block">
    <el-date-picker
      v-model="value"
      type="daterange"
      align="right"
      unlink-panels
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :picker-options="pickerOptions2"
    ></el-date-picker>

    <p class="text-center caption-color">不良率趋势图</p>
    <div ref="myChart" :style="{width: '95%', height: '300px'}"></div>

    <p class="text-center caption-color">分产品的不良率统计表</p>
    <el-table
      :data="tableData1"
      border
      style="width: 100%"
      :header-cell-style="hdStyle"
      :cell-style="cellStyle"
    >
      <el-table-column fixed="left" label="操作" width="80">
        <template slot-scope="scope">
          <el-button @click="getResDetail(scope.row)" type="text" size="small">查看详情</el-button>
        </template>
      </el-table-column>
      <el-table-column
        :fixed="index < 2 ? true : false"
        v-for="(item ,index) in tableCloumn1"
        width="150"
        :key="index"
        :prop="item.prop"
        :label="item.label"
      />
    </el-table>
    <el-pagination
      layout="prev, pager, next"
      :total="total"
      class="page"
      @current-change="currentChange"
      :current-page.sync="currentPage"
    />

    <el-dialog
      title="产品和工序的不良率统计表"
      :visible.sync="dialogVisible"
      width="80%"
      :before-close="handleClose"
    >
      <el-table
        :data="tableData2"
        border
        style="width: 100%"
        :header-cell-style="hdStyle"
        :cell-style="cellStyle"
      >
        <el-table-column fixed="left" label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="getResDetail1(scope.row)" type="text" size="small">查看详情</el-button>
          </template>
        </el-table-column>
        <el-table-column
          :fixed="index < 3 ? true : false"
          v-for="(item ,index) in tableCloumn2"
          width="150"
          :key="index"
          :prop="item.prop"
          :label="item.label"
        />
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="采集点的不良率统计"
      :visible.sync="dialogVisible1"
      width="80%"
      :before-close="handleClose"
    >
      <p>不良率采集点：</p>
      <p>检测人员：</p>
      <p>操作时间：</p>
      <p>产品编码：</p>
      <p>产品名称：</p>
      <p>问题归类：</p>
      <p>问题描述：</p>
      <p>突破：</p>
      <p></p>
      <!-- <el-table :data="tableData3" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
        <el-table-column v-for="(item ,index) in tableCloumn3" :key="index" :prop="item.prop" :label="item.label"/>
      </el-table>-->
      <span slot="footer" class="dialog-footer">
        <el-button @click="back">返回</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
const echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/toolbox");
export default {
  data() {
    return {
      hdStyle: {
        backgroundColor: "#fafafa",
        textAlign: "center"
      },
      cellStyle: {
        textAlign: "center"
      },
      total: 100,
      currentPage: 1,
      pickerOptions2: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近半年",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一年",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近十年",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(
                start.getTime() - 3600 * 1000 * 24 * (365 * 8 + 366 * 2)
              );
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value: "",
      tableCloumn1: [
        { prop: "col1", label: "产品编码" },
        { prop: "col2", label: "产品名称" },
        { prop: "col3", label: "2019-02-28" },
        { prop: "col4", label: "2019-03-01" },
        { prop: "col5", label: "2019-03-02" },
        { prop: "col6", label: "2019-03-03" },
        { prop: "col7", label: "2019-03-04" },
        { prop: "col8", label: "2019-03-05" }
      ],
      tableData1: [
        { col1: "1", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "2", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "3", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "4", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "5", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "6", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "7", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "8", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "9", col2: "", col3: "", col4: "", col5: "", col6: "" },
        { col1: "10", col2: "", col3: "", col4: "", col5: "", col6: "" }
      ],
      tableCloumn2: [
        { prop: "col1", label: "产品编码" },
        { prop: "col2", label: "产品名称" },
        { prop: "col3", label: "不良率采集点" },
        { prop: "col3", label: "2019-02-28" },
        { prop: "col4", label: "2019-03-01" },
        { prop: "col5", label: "2019-03-02" },
        { prop: "col6", label: "2019-03-03" },
        { prop: "col6", label: "2019-03-04" },
        { prop: "col6", label: "2019-03-05" }
      ],
      tableData2: [
        { col1: "1", col2: "", col3: "点1", col4: "", col5: "", col6: "" },
        { col1: "1", col2: "", col3: "点2", col4: "", col5: "", col6: "" },
        { col1: "1", col2: "", col3: "点3", col4: "", col5: "", col6: "" },
        { col1: "1", col2: "", col3: "点4", col4: "", col5: "", col6: "" },
        { col1: "1", col2: "", col3: "点5", col4: "", col5: "", col6: "" },
        { col1: "1", col2: "", col3: "点6", col4: "", col5: "", col6: "" }
      ],
      tableCloumn3: [
        { prop: "col1", label: "不良率采集点" },
        { prop: "col2", label: "检测人员" },
        { prop: "col3", label: "操作时间" },
        { prop: "col3", label: "产品编码" },
        { prop: "col4", label: "产品名称" },
        { prop: "col5", label: "问题归类" },
        { prop: "col6", label: "问题描述" },
        { prop: "col6", label: "突破" }
      ],
      tableData3: [
        { col1: "不良率", col2: "", col3: "", col4: "", col5: "", col6: "" }
      ],
      dialogVisible: false,
      dialogVisible1: false
    };
  },
  mounted() {
    this.drawBar();
  },
  methods: {
    drawBar() {
      const myChart = echarts.init(this.$refs.myChart);
      const xData = [
        "1月",
        "2月",
        "3月",
        "4月",
        "5月",
        "6月",
        "7月",
        "8月",
        "9月",
        "10月",
        "11月",
        "12月"
      ];
      const seriesData = [0, 0, 0, 0, 50, 100, 0, 0, 0, 50, 25, 10];
      const option = {
        color: ["#3398DB"],
        tooltip: { trigger: "axis", formatter: "{b0}<br/>{a0}: {c0}%" },
        legend: { data: ["不良率"] },
        toolbox: {
          show: true,
          feature: {
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ["line", "bar"] },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        calculable: true,
        xAxis: [{ type: "category", data: xData }],
        yAxis: [{ type: "value", name: "不良率（%）" }],
        grid: { left: 35 },
        series: [{ name: "不良率", type: "bar", data: seriesData }]
      };
      myChart.setOption(option);
    },
    change() {
      console.log("1");
      console.log(this.value);
    },
    currentChange() {
      console.log(this.currentPage)
    },
    getResDetail(row) {
      this.dialogVisible = true;
    },
    getResDetail1(row) {
      this.dialogVisible1 = true;
    },
    handleClose(done) {
      done();
    },
    back() {
      this.dialogVisible1 = false;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/ {
  .el-range-separator {
    width: 8%;
  }
}
.page {
  text-align: center;
}
</style>



